
header { 
    height: 106px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    background-color: #fff;
    z-index: 1000;
    transition: all .3s ease;
    .logo {
        margin-left: 100px;
    }

    .logo a {
        font-family: Gotham, Montserrat;
        font-style: normal;
        font-size: 16px;
        font-weight: 700;
        text-transform: uppercase;
        color: #171A27;
    }

    .menu-list {
        display: block;
        li {
        display: inline;
        margin-left: 30px;
        margin-right: 30px;
        }

        li a {
            color: #171A27;
            text-transform: uppercase;
            font-size: 16px;
            position: relative;
        }

        li .active:after, li a::after {
            content: '';
            width: 107%;
            height: 2px;
            background-color: #FF9781;
            position: absolute;
            bottom: -7px;
            transform: translate(-96%);
            opacity: 0;
            pointer-events: none;
            transition: all .3s ease;
        }

        li .active:after {
            opacity: 1;
        }

        li:hover a::after {
            bottom: -7px;
            opacity: 1;
        }
    }

    .login-lang-items {
        margin-right: 100px;
    }

    .burger-menu {
        display: none;
        icon {
            color: #000;
        }
    }
}

.scrolled-header { 
    -webkit-box-shadow: 0px 3px 5px 0px rgba(0,0,0,0.52);
    -moz-box-shadow: 0px 3px 5px 0px rgba(0,0,0,0.52);
    box-shadow: 0px 3px 5px 0px rgba(0,0,0,0.52);
}
