* {
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-family: 'Montserrat', sans-serif;
  line-height: 27px;
  box-sizing: border-box;
}

section {
  margin: 110px 0;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 42px;
}

h2 {
  font-size: 32px;
}

h3 {
  font-size: 20px;
}

input:focus, textarea:focus, button:focus {
  outline: none;
}

*:focus {
  outline: none;
}

ul {
  list-style-type: none;
}


.default-title {
  font-size: 32px;
  text-transform: uppercase;
  font-weight: bold;
}

button {
  outline: none;
  border: none;
}

.container {
  width: 1180px;  
  margin: 0 auto;
}

@media only screen and (max-width: 1199px) {
  * {
    font-size: 16px;
  }
  .container {
    width: 100%;
    min-width: 300px;
    max-width: 900px;
    padding: 0 20px;
  }
}

@media only screen and (max-width: 640px) {
  * {
    font-size: 14px;
  }

  section {
    margin: 70px 0;
  }
  
}